import React from 'react';

const NextHiroSection = () => {
  return (
    <header
      style={{
        backgroundColor: '#f1f5f9',
        padding: '4rem 1rem',
        fontFamily: 'Georgia, serif',
        textAlign: 'center',
      }}
    >
      <div style={{ maxWidth: '900px', margin: '0 auto' }}>
        <p style={{ fontSize: '1.1rem', lineHeight: '1.8', color: '#000000' }}><b>
          A modular, ethically aligned platform built for sovereignty, transparency, and meaningful exchanges.
          Every product is a public artifact. Every transaction is a traceable vow.</b>
        </p>
      </div>
    </header>
  );
};

export default NextHiroSection;
