import React from 'react';
import digitalInnovation from '../../img/digital-innovation.jpg';
import vitalAssets from '../../img/vital-assets.jpg';
import transparentGovernance from '../../img/transparent-governance.jpg';

const FutureProducts = () => {
  const products = [
    {
      id: 'fp001',
      title: ' Digital Innovation',
      description: 'Cutting-edge CLI tools and development resources for modern workflows.',
      image: digitalInnovation,
      link: '/products/audit-journal',
    },
    {
      id: 'fp002',
      title: ' Vital Assets',
      description: 'Essential products for sustainable living, wellness, and symbolic connection.',
      image: vitalAssets,
      link: '/products/identity-token',
    },
    {
      id: 'fp003',
      title: ' Transparent Governance',
      description: 'Democratic tools and frameworks for ethical decision-making.',
      image: transparentGovernance,
      link: '/products/dev-hoodie',
    },
  ];

  return (
    <section style={{
      padding: '3rem 1rem',
      backgroundColor: '#f9f9f9',
      fontFamily: 'Segoe UI, sans-serif',
      textAlign: 'center'
    }}>
      <h2 style={{ fontSize: '2rem', marginBottom: '2rem' }}> Foundation of Ethical Commerce</h2>
              <p style={{
        maxWidth: '700px',
        margin: '0 auto 3rem auto',
        fontSize: '1rem',
        color: '#555'
      }}>
          Our carefully curated categories represent the foundation of conscious business
          digital innovation, vital sustainability, and transparent governance working together
          to create a better commercial ecosystem.
        </p>
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        flexWrap: 'wrap',
        gap: '2rem'
      }}>
        {products.map((product) => (
          <a
            key={product.id}
            href={product.link}
            style={{
              textDecoration: 'none',
              color: 'inherit',
              width: '300px',
              borderRadius: '10px',
              overflow: 'hidden',
              boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
              backgroundColor: '#fff',
              transition: 'transform 0.2s ease-in-out'
            }}
          >
            <img
              src={product.image}
              alt={product.title}
              style={{ width: '100%', height: '180px', objectFit: 'cover' }}
            />
            <div style={{ padding: '1rem', textAlign: 'left' }}>
              <h3 style={{ margin: '0 0 0.5rem 0', fontSize: '1.2rem' }}>{product.title}</h3>
              <p style={{ fontSize: '0.95rem', color: '#555' }}>{product.description}</p>
            </div>
          </a>
        ))}
      </div>
    </section>
  );
};

export default FutureProducts;

